import React, { useEffect, useState } from 'react';
import axios from 'axios'
export const GlobalContext = React.createContext(null)

function GlobalProvider({ children }) {

    const [transactions, setTransactions] = useState([])
    const [busca, setBusca] = useState('')
    const [produtosFiltrados, setProdutosFiltrados] =  useState([])
    const [ loading, setLoading ] = useState(false);



    useEffect(() => {
      setLoading(true)
        axios
          .get("https://api.zeporlitro.online")
          .then((res) => {
            setLoading(false)
            res.data.sort(function(a,b) {
              return a.plitro < b.plitro ? -1 : a.plitro > b.plitro ? 1 : 0;
          }); 
            setTransactions(res.data);
            setProdutosFiltrados(res.data)
          })
          .catch((err) => {
            console.log(err);
          });
      }, []);

    useEffect(() => {   
        let filtro = transactions
        .filter((transaction) => transaction.title.toLowerCase().includes(lowerBusca)) 
        setProdutosFiltrados(filtro)
    }, [busca])

    
//filtrar table search
const lowerBusca = busca.toLocaleLowerCase()
const transactionsFiltradas = transactions
.filter((transaction) => transaction.title.toLowerCase().includes(lowerBusca)) 




    return (

        <GlobalContext.Provider value={{transactions, setTransactions, busca, setBusca, produtosFiltrados, loading}}>

            {children}

        </GlobalContext.Provider>

    );
    
}

export default GlobalProvider;