
import React, {useState, useEffect} from "react"
import { Container, Content, Search, Logo } from "./styles"
import {GlobalContext} from '../../context/GlobalContext'



export default function Header(){
    const {busca, setBusca} = React.useContext(GlobalContext)
    return( 
        <>
        <Container>
            <Content>
                <Logo>
            <h1>ZéporLitro</h1>
            </Logo>
            <Search>
            <input type='text' placeholder="Pesquise a sua cerveja favorita" onChange={(ev)=> setBusca(ev.target.value)} value={busca}></input>
            </Search>
            </Content>
        </Container>
        </>
    )
}